'use client';

import { useState, useRef } from 'react';
import { useTranslations } from 'next-intl';
import { Card } from '@/app/components/ui/Card';

interface TweetPreviewClientProps {
    locale: string;
}

export default function TweetPreviewClient({ locale }: TweetPreviewClientProps) {
    const t = useTranslations('Tools.tweet-preview');
    const [name, setName] = useState('Name');
    const [handle, setHandle] = useState('username');
    const [text, setText] = useState('This is a preview of your tweet. You can edit this text to see how it looks.');
    const [image, setImage] = useState<string | null>(null);
    const [verified, setVerified] = useState(true);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setImage(reader.result as string);
            };
            reader.readAsDataURL(file);
        }
    };

    const currentDate = new Date().toLocaleDateString(locale, {
        hour: 'numeric',
        minute: 'numeric',
        year: 'numeric',
        month: 'short',
        day: 'numeric'
    });

    return (
        <div className="space-y-6 max-w-4xl mx-auto">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                {/* Controls */}
                <Card padding="lg" className="space-y-4 h-fit">
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                            {t('ui.name')}
                        </label>
                        <input
                            type="text"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            className="w-full p-3 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                            {t('ui.handle')}
                        </label>
                        <div className="relative">
                            <span className="absolute left-3 top-3 text-gray-500">@</span>
                            <input
                                type="text"
                                value={handle}
                                onChange={(e) => setHandle(e.target.value)}
                                className="w-full p-3 pl-8 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                            />
                        </div>
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                            {t('ui.text')}
                        </label>
                        <textarea
                            value={text}
                            onChange={(e) => setText(e.target.value)}
                            className="w-full p-3 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent h-32 resize-none"
                            maxLength={280}
                        />
                        <div className="text-right text-sm text-gray-500">
                            {text.length} / 280
                        </div>
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">
                            {t('ui.uploadImage')}
                        </label>
                        <input
                            type="file"
                            accept="image/*"
                            onChange={handleImageUpload}
                            ref={fileInputRef}
                            className="w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100"
                        />
                        {image && (
                            <button
                                onClick={() => {
                                    setImage(null);
                                    if (fileInputRef.current) fileInputRef.current.value = '';
                                }}
                                className="mt-2 text-sm text-red-600 hover:text-red-800"
                            >
                                Remove Image
                            </button>
                        )}
                    </div>
                    <div className="flex items-center">
                        <input
                            type="checkbox"
                            id="verified"
                            checked={verified}
                            onChange={(e) => setVerified(e.target.checked)}
                            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                        />
                        <label htmlFor="verified" className="ml-2 block text-sm text-gray-900">
                            {t('ui.verified')}
                        </label>
                    </div>
                </Card>

                {/* Preview */}
                <div className="space-y-2">
                    <label className="block text-sm font-medium text-gray-700">
                        {t('ui.preview')}
                    </label>
                    <div className="bg-white border border-gray-200 rounded-xl p-4 max-w-[600px] mx-auto shadow-sm">
                        <div className="flex space-x-3">
                            <div className="flex-shrink-0">
                                <div className="h-12 w-12 rounded-full bg-gray-200 flex items-center justify-center text-xl font-bold text-gray-500">
                                    {name.charAt(0).toUpperCase()}
                                </div>
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center justify-between">
                                    <div className="flex items-center text-[15px]">
                                        <span className="font-bold text-gray-900 truncate mr-1">{name}</span>
                                        {verified && (
                                            <svg viewBox="0 0 24 24" aria-label="Verified account" className="w-5 h-5 text-blue-500 fill-current mr-1">
                                                <g><path d="M22.5 12.5c0-1.58-.875-2.95-2.148-3.6.154-.435.238-.905.238-1.4 0-2.21-1.71-3.998-3.818-3.998-.47 0-.92.084-1.336.25C14.818 2.415 13.51 1.5 12 1.5s-2.816.917-3.437 2.25c-.415-.165-.866-.25-1.336-.25-2.11 0-3.818 1.79-3.818 4 0 .495.083.965.238 1.4-1.272.65-2.147 2.018-2.147 3.6 0 1.495.782 2.798 1.942 3.486-.02.17-.032.34-.032.514 0 2.21 1.708 4 3.818 4 .47 0 .92-.086 1.335-.25.62 1.334 1.926 2.25 3.437 2.25 1.512 0 2.818-.916 3.437-2.25.415.163.865.248 1.336.248 2.11 0 3.818-1.79 3.818-4 0-.174-.012-.344-.033-.513 1.158-.687 1.943-1.99 1.943-3.484zm-6.616-3.334l-4.334 6.5c-.145.217-.382.334-.625.334-.143 0-.288-.04-.416-.126l-.115-.094-2.415-2.415c-.293-.293-.293-.768 0-1.06s.768-.294 1.06 0l1.77 1.767 3.825-5.74c.23-.345.696-.436 1.04-.207.346.23.44.696.21 1.04z"></path></g>
                                            </svg>
                                        )}
                                        <span className="text-gray-500 truncate">@{handle}</span>
                                        <span className="text-gray-500 mx-1">Â·</span>
                                        <span className="text-gray-500">1m</span>
                                    </div>
                                    <div className="text-gray-500">
                                        <svg viewBox="0 0 24 24" aria-hidden="true" className="w-5 h-5 fill-current"><g><path d="M3 12c0-1.1.9-2 2-2s2 .9 2 2-.9 2-2 2-2-.9-2-2zm9 2c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm7 0c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2z"></path></g></svg>
                                    </div>
                                </div>
                                <div className="text-[15px] text-gray-900 whitespace-pre-wrap mb-3">
                                    {text}
                                </div>
                                {image && (
                                    <div className="mb-3 rounded-2xl overflow-hidden border border-gray-200">
                                        <img src={image} alt="Tweet attachment" className="w-full h-auto object-cover max-h-[400px]" />
                                    </div>
                                )}
                                <div className="flex justify-between text-gray-500 max-w-[425px]">
                                    <div className="flex items-center group cursor-pointer hover:text-blue-500">
                                        <div className="p-2 group-hover:bg-blue-50 rounded-full transition-colors">
                                            <svg viewBox="0 0 24 24" aria-hidden="true" className="w-[18px] h-[18px] fill-current"><g><path d="M1.751 10c0-4.42 3.584-8 8.005-8h4.366c4.49 0 8.129 3.64 8.129 8.13 0 2.96-1.607 5.68-4.196 7.11l-8.054 4.46v-3.69h-.067c-4.49.1-8.183-3.51-8.183-8.01zm8.005-6c-3.317 0-6.005 2.69-6.005 6 0 3.37 2.77 6.08 6.138 6.01l.351-.01h1.761v2.3l5.087-2.81c1.951-1.08 3.163-3.13 3.163-5.36 0-3.39-2.744-6.13-6.129-6.13H9.756z"></path></g></svg>
                                        </div>
                                        <span className="text-xs ml-1">24</span>
                                    </div>
                                    <div className="flex items-center group cursor-pointer hover:text-green-500">
                                        <div className="p-2 group-hover:bg-green-50 rounded-full transition-colors">
                                            <svg viewBox="0 0 24 24" aria-hidden="true" className="w-[18px] h-[18px] fill-current"><g><path d="M4.5 3.88l4.432 4.14-1.364 1.46L5.5 7.55V16c0 1.1.9 2 2 2H19v2H7.5c-2.209 0-4-1.79-4-4V7.55L1.432 9.48.068 8.02 4.5 3.88zM16.5 6H11V4h5.5c2.209 0 4 1.79 4 4v8.45l2.068-1.93 1.364 1.46-4.432 4.14-4.432-4.14 1.364-1.46 2.068 1.93V8c0-1.1-.9-2-2-2z"></path></g></svg>
                                        </div>
                                        <span className="text-xs ml-1">12</span>
                                    </div>
                                    <div className="flex items-center group cursor-pointer hover:text-pink-500">
                                        <div className="p-2 group-hover:bg-pink-50 rounded-full transition-colors">
                                            <svg viewBox="0 0 24 24" aria-hidden="true" className="w-[18px] h-[18px] fill-current"><g><path d="M16.697 5.5c-1.222-.06-2.679.51-3.89 2.16l-.805 1.09-.806-1.09C9.984 6.01 8.526 5.44 7.304 5.5c-1.243.07-2.349.78-2.91 1.91-.552 1.12-.633 2.78.479 4.82 1.074 1.97 3.257 4.27 7.129 6.61 3.87-2.34 6.052-4.64 7.126-6.61 1.111-2.04 1.03-3.7.477-4.82-.561-1.13-1.666-1.84-2.908-1.91zm4.187 7.69c-1.351 2.48-4.001 5.12-8.379 7.67l-.503.3-.504-.3c-4.379-2.55-7.029-5.19-8.382-7.67-1.36-2.5-1.41-4.86-.514-6.67.887-1.79 2.647-2.91 4.601-3.01 1.651-.09 3.368.56 4.798 2.01 1.429-1.45 3.146-2.1 4.796-2.01 1.954.1 3.714 1.22 4.601 3.01.896 1.81.846 4.17-.514 6.67z"></path></g></svg>
                                        </div>
                                        <span className="text-xs ml-1">148</span>
                                    </div>
                                    <div className="flex items-center group cursor-pointer hover:text-blue-500">
                                        <div className="p-2 group-hover:bg-blue-50 rounded-full transition-colors">
                                            <svg viewBox="0 0 24 24" aria-hidden="true" className="w-[18px] h-[18px] fill-current"><g><path d="M8.75 21V3h2v18h-2zM18 21V8.5h2V21h-2zM4 21l.004-10h2L6 21H4zm9.248 0v-7h2v7h-2z"></path></g></svg>
                                        </div>
                                        <span className="text-xs ml-1">1.2K</span>
                                    </div>
                                    <div className="flex items-center group cursor-pointer hover:text-blue-500">
                                        <div className="p-2 group-hover:bg-blue-50 rounded-full transition-colors">
                                            <svg viewBox="0 0 24 24" aria-hidden="true" className="w-[18px] h-[18px] fill-current"><g><path d="M12 2.59l5.7 5.7-1.41 1.42L13 6.41V16h-2V6.41l-3.3 3.3-1.41-1.42L12 2.59zM21 15l-.02 3.51c0 1.38-1.12 2.49-2.5 2.49H5.5C4.11 21 3 19.88 3 18.5V15h2v3.5c0 .28.22.5.5.5h12.98c.28 0 .5-.22.5-.5L19 15h2z"></path></g></svg>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="text-center text-sm text-gray-500">
                {t('ui.processingNote')}
            </div>
        </div>
    );
}
